package com.sunplacestudio.dalerdeveloper.pages

import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import com.varabyte.kobweb.core.Page
import kotlinx.browser.window

private var isDesktop by mutableStateOf(isDesktop())

@Page
@Composable
fun MainPage() {
    addWidthChangeListener()
    if (isDesktop) {
        MainPageDesktop()
    } else {
        MainPageMobile()
    }
}

private fun isDesktop(): Boolean {
    val agent = window.navigator.userAgent.lowercase()
    return !isContainsMobile(agent)
}

private fun isContainsMobile(agent: String): Boolean {
    val lower = agent.lowercase()
    return lower.contains("android") ||
            lower.contains("webos") ||
            lower.contains("iphone") ||
            lower.contains("ipad") ||
            lower.contains("ipod") ||
            lower.contains("blackberry") ||
            lower.contains("windows phone")
}

private fun addWidthChangeListener() {
    window.addEventListener(type = "resize", callback = {
        isDesktop = isDesktop()
    })
}

