package com.sunplacestudio.dalerdeveloper.common

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import kotlinx.browser.document
import org.jetbrains.compose.web.css.CSSNumeric
import org.jetbrains.compose.web.css.px

@Composable
fun StackItem(text: String, textSize: Int, start: CSSNumeric = 0.px, end: CSSNumeric = 10.px) {
    Box(
        modifier = Modifier.padding(
            left = start, right = end, top = 5.px, bottom = 5.px
        )
    ) {
        Box(
            modifier = Modifier.borderRadius(10.px).backgroundColor(getColor(0xf1f1f1)).padding(all = 7.px),
            contentAlignment = Alignment.Center
        ) {
            SpanText(
                text = text,
                modifier = Modifier.fontFamily(Fonts.ROBOTO_BOLD.text)
                    .fontWeight(800)
                    .fontSize(textSize.px)
                    .color(getColor(0x292929))
            )
        }
    }
}


fun Modifier.applyNewLine(): Modifier {
    styleModifier {
        property("white-space", "pre-wrap")
    }
    return this
}

fun getColor(value: Int) = com.varabyte.kobweb.compose.ui.graphics.Color.Companion.rgb(value)

fun getColor(r: Int, g: Int, b: Int, a: Float) =
    com.varabyte.kobweb.compose.ui.graphics.Color.Companion.rgba(r, g, b, a)

fun scrollToId(id: String) {
    document.getElementById(id)?.scrollIntoView(js("{behavior: \"smooth\"}"))
}