@Composable
private fun Project(enum: ProjectsEnum) {
    Row(
        modifier = Modifier.padding(
            top = 10.px, bottom = 10.px
        )
    ) {
        Box(
            contentAlignment = Alignment.Center
        ) {
            Image(
                src = "background_image.svg", modifier = Modifier.size(390.px).borderRadius(20.px)
            )
            if (enum.image != null) {
                Image(
                    src = enum.image, modifier = Modifier.size(390.px).width(177.px)
                )
            } else if (enum.textIfImageNull != null) {
                SpanText(
                    text = enum.textIfImageNull,
                    modifier = Modifier.color(getColor(0x292929))
                        .width(390.px)
                        .fontSize(40.px)
                        .fontFamily(Fonts.ROBOTO.text)
                        .textAlign(TextAlign.Center)
                )
            }
        }
        Column(
            modifier = Modifier.width(20.percent).padding(left = 40.px)
        ) {
            SpanText(
                text = enum.date,
                modifier = Modifier.color(getColor(0x292929)).fontSize(18.px).fontFamily(Fonts.ROBOTO.text)
            )
            Row(
                modifier = Modifier.padding(top = 20.px).flexWrap(FlexWrap.Wrap)
            ) {
                enum.stack.forEach { StackItem(it, STACK_TEXT_SIZE) }
            }
        }
        Column(
            modifier = Modifier.width(40.percent).padding(left = 20.px).height(390.px)
        ) {
            SpanText(
                text = enum.appName,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(40.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .fillMaxWidth()
                    .padding(top = 1.px)
            )
            SpanText(
                text = enum.description,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(25.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .fillMaxWidth()
                    .padding(top = 20.px)
            )
            Box(
                modifier = Modifier.fillMaxHeight(), contentAlignment = Alignment.BottomStart
            ) {
                enum.link?.let {
                    Link(
                        path = it,
                        text = Strings.LinkToProject.text,
                        modifier = Modifier.color(getColor(0x292929))
                            .fontSize(20.px)
                            .fontFamily(Fonts.ROBOTO.text)
                            .fillMaxWidth()
                            .height(40.px)
                    )
                }
            }
        }
    }
}

