@Composable
private fun Menu() {
    if (isShowMenu) {
        Column(
            modifier = Modifier.fillMaxWidth()
                .backgroundColor(Color.white)
                .padding(bottom = 10.px),
            verticalArrangement = Arrangement.Center
        ) {
            MenuItem(top = 0, text = Strings.WORK_EXPERIENCE.text) {
                scrollToId(IdsMobile.WORK.id)
            }
            MenuItem(Strings.Education.text) {
                scrollToId(IdsMobile.EDUCATION.id)
            }
            MenuItem(Strings.Language.text) {
                scrollToId(IdsMobile.LANGUAGE.id)
            }
            MenuItem(Strings.Interests.text) {
                scrollToId(IdsMobile.INTERESTS.id)
            }
            MenuItem(Strings.Projects.text) {
                scrollToId(IdsMobile.PROJECTS.id)
            }
            MenuItem(Strings.Contacts.text) {
                scrollToId(IdsMobile.CONTACTS.id)
            }
        }
    }
}

@Composable
private fun MenuItem(text: String, top: Int = 10, onClick: () -> Unit) {
    Button(onClick = {
        onClick.invoke()
    }, modifier = Modifier.backgroundColor(Color.transparent).fillMaxWidth()) {
        SpanText(
            text = text,
            modifier = Modifier.fillMaxWidth()
                .textAlign(TextAlign.Center)
                .fontSize(16.px)
                .fontFamily(Fonts.ROBOTO_LIGHT.text)
                .fontWeight(600)
                .padding(top = top.px, bottom = 10.px)
        )
    }
}

@Composable
private fun HeaderMenu() {
    Column(modifier = Modifier.padding(left = PADDING, right = PADDING).width(widthScreenPx)) {
        Button(
            onClick = { isShowMenu = !isShowMenu },
            modifier = Modifier.padding(top = 17.px, bottom = 20.px)
