                .id(IdsMobile.MENU.id)
        ) {
            Image(src = "menu.svg", modifier = Modifier.size(38.px))
        }
        Menu()
    }
}


@Composable
private fun Header() {
    Box(
        modifier = Modifier.width(widthScreenPx)
    ) {
        Box(
            contentAlignment = Alignment.CenterEnd,
            modifier = Modifier.width(widthScreenPx).padding(top = 150.px, right = 0.px, left = 0.px, bottom = 0.px)
        ) {
            val width = widthScreen * 0.8
            val height = 221 * width / 202
            Image(
                src = "mobile.svg", modifier = Modifier.width(width.px).height(height.px)
            )
        }
        Column(modifier = Modifier.padding(left = PADDING)) {
            SpanText(
                text = Strings.NAME.text,
                modifier = Modifier.fontSize(32.px)
                    .fontWeight(800)
                    .color(getColor(0xD9EFF3)),
            )
            SpanText(
                text = Strings.LEVEL.text,
                modifier = Modifier.fontSize(16.px).fontFamily(Fonts.ROBOTO_LIGHT.text)
            )
            SpanText(
                text = Strings.VISA_AND_LOCATION.text,
                modifier = Modifier.fontSize(16.px)
                    .fontFamily(Fonts.ROBOTO_LIGHT.text)
                    .padding(top = 15.px, bottom = 10.px)
                    .fontWeight(500)
                    .styleModifier {
                        property("white-space", "pre-wrap")
                    }
            )
            Row(
                modifier = Modifier.width((widthScreen * 0.6).px).padding(top = 20.px).flexWrap(FlexWrap.Wrap)
            ) {
                Skills.values().forEach {
                    StackItem(it.text, STACK_TEXT_SIZE)
                }
            }
            SpanText(
                text = Strings.START_TEXT.text,
                modifier = Modifier.fontSize(16.px)
                    .fontFamily(Fonts.ROBOTO_LIGHT.text)
                    .fontWeight(300)
                    .padding(top = 20.px, right = PADDING)
                    .lineHeight(28.px)
                    .color(getColor(0x292929))
            )
        }
    }
}

