    ) {
        Column {
            SpanText(
                text = position,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(24.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .fontStyle(FontStyle.Normal)
                    .fontWeight(800)
            )
            SpanText(
                text = company,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(18.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .fontStyle(FontStyle.Normal)
                    .fontWeight(600)
            )
            SpanText(
                text = period,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(18.px)
                    .fontFamily(Fonts.ROBOTO_MEDIUM.text)
                    .fontStyle(FontStyle.Normal)
                    .fontWeight(500)
                    .padding(top = 20.px)
            )
            SpanText(
                text = responsible,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(18.px)
                    .fontFamily(Fonts.ROBOTO_LIGHT.text)
                    .fontWeight(300)
            )
            SpanText(
                text = Strings.AchievementsTasks.text,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(18.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .fontStyle(FontStyle.Normal)
                    .fontWeight(500)
                    .padding(top = 20.px)
            )
            tasks.forEach {
                SpanText(
                    text = "- $it",
