@Composable
private fun Project(enum: ProjectsEnum) {
    Column(
        modifier = Modifier.padding(
            top = 10.px, bottom = 10.px
        )
    ) {
        SpanText(
            text = enum.appName,
            modifier = Modifier.color(getColor(0x292929))
                .fontSize(24.px)
                .fontFamily(Fonts.ROBOTO.text)
                .fontWeight(800)
                .padding(top = 1.px, right = PADDING)
        )
        SpanText(
            text = enum.date,
            modifier = Modifier.color(getColor(0x292929))
                .fontSize(18.px)
                .fontWeight(500)
                .fontFamily(Fonts.ROBOTO.text)
                .padding(top = 5.px)
        )
        Row(
            modifier = Modifier.padding(top = 20.px, bottom = 20.px).flexWrap(FlexWrap.Wrap)
        ) {
            enum.stack.forEach { StackItem(text = it, STACK_TEXT_SIZE) }
        }
        Box(
            modifier = Modifier.padding(right = PADDING), contentAlignment = Alignment.Center
        ) {
            val size = widthScreen - 28 * 2
            Image(
                src = "background_image.svg", modifier = Modifier.size(size.px).borderRadius(20.px)
            )
            if (enum.image != null) {
                Image(
                    src = enum.image, modifier = Modifier.size(size.px).width((size / 2.2).px)
                )
            } else if (enum.textIfImageNull != null) {
                SpanText(
                    text = enum.textIfImageNull,
                    modifier = Modifier.color(getColor(0x292929))
                        .width(size.px)
                        .fontSize(40.px)
                        .padding(left = 30.px, right = 30.px)
                        .fontFamily(Fonts.ROBOTO.text)
                        .textAlign(TextAlign.Center)
                )
            }
        }
        SpanText(
            text = enum.description,
            modifier = Modifier.color(getColor(0x292929))
                .fontSize(16.px)
                .fontFamily(Fonts.ROBOTO.text)
                .padding(top = 20.px, right = PADDING)
        )
        if (enum.link != null) {
            Link(
                path = enum.link,
                text = Strings.LinkToProject.text,
                modifier = Modifier.color(getColor(0x292929))
                    .fontSize(20.px)
                    .padding(top = 10.px)
                    .fontFamily(Fonts.ROBOTO.text)
                    .height(40.px)
            )
        }
    }
}

